
import axios from 'axios'
import Xheader from '@/components/comother/rheader'
import Vue from 'vue';
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

Vue.component(Popup.name, Popup);
export default {
data(){

return {
contents:{rights:'',title:'支部党员'},
list:"",
isYellow:false,
showPop:false
}
}, filters: {
Upper: function (value) {
try {
if(value===null) throw "去处理";
if(value=== 1)  throw "已审核";
if(value=== 0)  throw "待审核";
}
catch(err) {
return value=err;
}

},
Upper1: function (value) {
try {
if(value===null) throw "";
if(value=== 2) throw "yellowA";
if(value=== 1)  throw "yellowB";
if(value=== 0)  throw "yellowC";
}
catch(err) {
return value=err;
}

}
},
components:{
'r-header':Xheader,
ViewBox,
Popup
},
directives:{
TransferDom
},
methods:{
getlist(){
axios({
method: 'get',
url: 'ppartymember/getPartymemberByDepartmentid',
params: {
departmentid:1
}
}) .then((res)=> {
console.log(res)
this.list=res.data


})
.catch(function (error) {
console.log(error);
});
},
changeItem(item){
this.$router.push({
path: '/points/pointEvaluate',
name: 'pointEvaluate',
params: {
partmentId:item.id,
name:encodeURI(item.name),
departmentId:item.departmentid,
userId:item.userid
}
})
},
know(){
this.showPop = false
}
},
mounted(){
this.getlist()

}
}



